
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  nextTick,
  computed,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import {
  BoxplotChart,
  BoxplotSeriesOption,
  ScatterChart,
  ScatterSeriesOption
} from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption,
  DatasetComponent,
  DatasetComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { BoxPlotResponse } from "@/typings/brandInsight";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";

type ECOption = echarts.ComposeOption<
  | BoxplotSeriesOption
  | GridComponentOption
  | TooltipComponentOption
  | DatasetComponentOption
  | ScatterSeriesOption
>;

echarts.use([
  BoxplotChart,
  ScatterChart,
  GridComponent,
  TooltipComponent,
  DatasetComponent,
  CanvasRenderer
]);

const Component = defineComponent({
  name: "BoxplotChartCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object as PropType<BoxPlotResponse>,
      default: () => ({
        categories: [],
        data: []
      })
    }
  },
  setup(props) {
    const { title, data } = toRefs(props);
    const store = useStore();
    const refComponent = ref(null);
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.data.length;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      option = {
        tooltip: {
          trigger: "axis",
          formatter(param: any) {
            return `
            <div style="min-width: 200px;">
              <h3>${param[0].name}</h3>
              ${param
                .filter((item: any) => item.componentSubType === "boxplot")
                .map(
                  (item: any) =>
                    `
                      <div style="display: flex; justify-content: space-between;">
                        <span>${item.marker} ${item.name} (Median) ${item.data[3]}</span> <br />
                      </div>
                    `
                )
                .join("")}
            </div>
          `;
          }
        },
        grid: {
          left: "20%"
        },
        dataset: [
          {
            source: data.value.data
          },
          {
            transform: {
              type: "boxplot",
              config: {
                itemNameFormatter(params: { value: number }) {
                  return data.value.categories[params.value];
                }
              }
            }
          },
          {
            fromDatasetIndex: 1,
            fromTransformResult: 1
          }
        ],
        xAxis: {
          name: "Price",
          nameLocation: "middle",
          nameGap: 30,
          scale: true
        },
        yAxis: {
          type: "category",
          data: data.value.categories,
          axisLabel: {
            formatter(value: any) {
              const SPLIT_MAX = 10;
              if (value.length > SPLIT_MAX) {
                return value.slice(0, SPLIT_MAX - 1) + "...";
              }
              return value;
            }
          }
        },
        series: [
          {
            name: "boxplot",
            type: "boxplot",
            datasetIndex: 1
          },
          {
            name: "outlier",
            type: "scatter",
            encode: { x: 1, y: 0 },
            datasetIndex: 2,
            symbolSize: 5,
            itemStyle: {
              color: "#20bfa9"
            }
          }
        ]
      };

      await nextTick();
      const chart = echarts.init(
        document.getElementById("boxplot-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      if (!option) return;
      dialogVisible.value = true;

      await nextTick();
      const chart = echarts.init(
        document.getElementById("boxplot-chart-dialog-" + title.value)!
      );
      chart.setOption({
        ...option,
        yAxis: {
          ...option.yAxis,
          // @ts-ignore
          axisLabel: { ...option.yAxis.axisLabel, interval: 0 }
        }
      });
      store.commit(AddResizer, chart);
    };

    return {
      refComponent,
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
