<template>
  <div id="brand-summary-component">
    <el-row :gutter="30" class="brand-info-container">
      <div class="brand-card">
        <img
          class="cover"
          :src="formatImageUrl(summary && summary.logo)"
          alt=""
        />
        <div class="context">
          <p v-for="(p, index) in summary ? summary.biography.split('\n') : []" :key="index">
            {{ p }}
          </p>
        </div>
      </div>
      <PowerChart v-if="summary" :data="summary.power_scores"></PowerChart>
    </el-row>

    <h2 class="zoom-title">Brand Summary</h2>
    <div class="summary-container">
      <div class="context">
        <p v-for="(p, index) in summary ? summary.summary.split('\n') : []" :key="index">
          {{ p }}
        </p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, watchEffect, ref } from "vue";

import PowerChart from "@/components/PowerChart.vue";
import { getBrandSummary } from "@/apis/brand";
import { BrandSummaryRespones } from "@/typings/brandInsight";
import { formatImageUrl } from "@/utils";

const Component = defineComponent({
  name: "BrandSummary",
  components: {
    PowerChart
  },
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const { brand } = toRefs(props);
    const summary = ref<BrandSummaryRespones | null>(null);

    watchEffect(async () => {
      if (!brand.value) return;

      const { data } = await getBrandSummary(brand.value);
      summary.value = data;
    });

    return {
      summary,

      formatImageUrl
    };
  }
});

export default Component;
</script>

